

import React, { lazy,Suspense,Component } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import {
  CHeader,
  CToggler,
  CHeaderBrand,
  CHeaderNav,
  CBadge,
  CButton,
  CButtonGroup,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCol,
  CProgress,
  CRow,
  CCallout
} from '@coreui/react'
import {
  Card,
  Col,
  Container,
  Row,
  Spinner,
  CardBody,
  CardHeader,
  CardTitle,
  FormGroup,
  Label,
  Button,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Breadcrumb,
  BreadcrumbItem,
} from "reactstrap"
import CIcon from '@coreui/icons-react'

import Carousel from 'react-bootstrap/Carousel'

import { CContainer, CFade } from '@coreui/react'
import  { Redirect } from 'react-router-dom'

import TheHeaderVisiteur from '../../containers/TheHeaderVisiteur'
import TheFooter from '../../containers/TheFooter'

import conf from '../../conf.js';
import { storage } from "../../firebase/firebase";

import $ from 'jquery'

const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse"></div>
  </div>
)


class Apropos extends Component {
  constructor(props) {
      super(props)
      this.state = {
        textApropos:"",
        ok:"",
        titreApropos:"",
        idApropos:0,
        telephone:"",
        email:"",
        adresse:"",
        service:[],
        video:"",
        video_joutube:"",
        page:<div style={{marginTop:"25%",marginBottom:"20%",textAlign:"center"}}>
        <Spinner type="grow" color="success" />
     </div>,
       status:true,
      }
  }
  
  componentDidMount() {
    const requestOptions = {
      method: 'GET',
     
    };
  
    fetch(conf.url+"acceuil/propos/propos",requestOptions)
    .then((response) => response.json())
    .then((data) => {
       console.log("propos====>",data)
       this.setState({status:false})
       try{
        this.setState({titreApropos:data[0].titre})
        this.setState({textApropos:data[0].text})
        this.setState({adresse:data[0].adresse})
        this.setState({telephone:data[0].telephone})
        this.setState({email:data[0].email})
        this.setState({video_joutube:data[0].urlVideo})
        this.setState({idApropos:data[0].id})
        this.setState({status:false})
       }catch(error){
  
       }
       
     }
     
    );

    fetch(conf.url+"acceuil/fichier/fichiers",requestOptions)
    .then((response) => response.json())
    .then((data) => {
      console.log("fichier====>",data)
    
      
       data.map((el,idx)=>{
         try{
            if(el.multimedia){
            storage.ref("images")
            .child(el.multimedia.file).getDownloadURL()
            .then((url) => {
                if(el.typeFichier==="VIDEO_PRESENTATION"){
                  this.setState({video:url})
				  console.log("fichierZErrr====>",url)
                }
               
            
            })
            }
      }catch(error){
        this.setState({video:""})
      }
       })
      
      
          
      
     }
     
    );




  };

 

  render() {

    return (
      <>
        {this.state.ok}
        <CContainer className="mt-5" fluid>
          <Suspense fallback={loading}>
          {
          (!this.state.status)?

          (
            <CFade>

              {/* <TheHeaderVisiteur/> */}

              <div className="mt-5"></div>
              <section id="about" class="about">
                    

                      <div class="section-title mt-5">
                        <h2>A propos</h2>
                      </div>

                      <div class="row">
                        <div class="col-lg-6 order-1 order-lg-2">
                          {/* <img src={aboutImg} class="img-fluid" alt=""/> */}
                          <div className="embed-responsive embed-responsive-16by9 mb-4">
                            <iframe className="embed-responsive-item" src={this.state.video} height="100" width="200" allowfullscreen frameborder="4"></iframe>
                          </div>
                        </div>
                        <div class="col-lg-6 pt-4 pt-lg-0 order-2 order-lg-1 content">
                          <h3 class="text-center">{this.state.titreApropos}</h3>
                          <p class="text-justify">
                          {this.state.textApropos}
                          </p>
                        </div>
                      </div>
                   
                  </section>
              
              {/* <TheFooter/> */}

            </CFade>):(<>{this.state.page}</>)
            }
          </Suspense>
        </CContainer>
      </>
    )
  }
}

export default Apropos
